
.card-container:nth-of-type(odd) .card-content {
  flex-direction: row-reverse;
}

.card-container {
  --card-height: 90%;
  --card-width: 45%;

  --card-container-height:60vh;
  --card-container-width:85%;

  --p-fontsize:2rem;
  --h1-fontsize:4rem;

  display: flex;
  width: var(--card-container-width);
  min-height: var(--card-container-height);
  margin: 20px 0 100px 0;
  position: relative;
  padding: 30px 40px 40px 40px;

  font-family: SpaceGrotesk, serif;

  .card-container-background {
    background-color: #F6EED6;
    z-index: -1;
    min-height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 20px;
  }

  .card-content {
    z-index: 1;
    background-color: transparent;
    box-sizing: border-box;
    width: 100%;
    min-height: 100%;

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5%;

    .card{
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      background-color: lightgray;
      object-fit: contain;
      box-sizing: border-box;
      position: relative;
      display: flex;
      width: var(--card-width);
      height: var(--card-height);
      border-radius: 10px;
      align-items: center;
      justify-content: center;
      transition: 500ms ease;
      z-index: 1;
    }
    .hover {
      &:hover {
        transform: scale(1.05);
        transition: transform 500ms ease;
      }
    }

    .description-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      width: 60%;
      height: 100%;

      h1 {
        color: black;
        font-size: var(--h1-fontsize);
        text-decoration: underline 10px solid;
        margin-top: 0;
      }

      .description {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        z-index: 0;
        border-radius: 10px;
        color: black;
        padding: 0;
        .paragraph{
          font-size: var(--p-fontsize);
          flex-basis: 70%;
          }
        .list{
          font-size: var(--p-fontsize);
          flex-basis: 30%;
          margin-top: 0;
        }
        &::after{
          content:"";

        }
      }
    }
  }

  .expanded-card-wrapper {
    position: absolute;
    width: var(--card-width);
    height: var(--card-height);
    z-index: 3;
  }

}
@font-face {
  font-family: 'SpaceGrotesk';
  src: url("../../../public/font/SpaceGrotesk-VariableFont_wght.ttf");
}
@font-face {
  font-family: 'Raleway';
  src: url("../../../public/font/Raleway-VariableFont_wght.ttf");
}